const Skeleton = () => {
    return (
        <div className="skeleton">
            <div className="skeleton-pfp"></div>
            <div style={{ width: Math.floor(Math.random() * 50) + 100 }} className="skeleton-username"></div>
            <div>
                <div style={{ width: "10%" }} className="skeleton-content-1 sl"></div>
                <div style={{ width: "30%" }} className="skeleton-content-2 sl"></div>
                <div style={{ width: "15%" }} className="skeleton-content-3 sl"></div>
                <div style={{ width: "25%" }} className="skeleton-content-4 sl"></div>
            </div>
            {Math.random() < 0.3 &&
                <div className="random-sl">
                    <div className="skeleton-content-5 sl"></div>
                    <div className="skeleton-content-6 sl"></div>
                </div>
            }
            {Math.random() < 0.1 && <div className="skeleton-content-a"></div>}
        </div>
    )
}

export default function SkeletonLoadingMessage() {
    return [...Array(20)].map((_, i) => <Skeleton key={i} />)
}