import { useContext, useState } from "react";
import { UserContext } from "../../app/message";
import Plus from "../../icons/plus";
import { link } from "../../config.json"
import Utils from "../../utils";
import Hashtag from "../../icons/hashtag";

const CreateChannel = ({ server, quit, animation }) => {
    const user = useContext(UserContext)
    const [settings, setSettings] = useState({ name: "", type: "text" })
    const [waiting, setWaiting] = useState("")

    const handleInputChange = (e) => setSettings(prev => ({ name: e.target.value, type: prev.type }))

    const create = async () => {
        if (!settings.name) return
        setWaiting("waiting")
        await fetch(`${link}servers/${server.id}/channels`, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                Authorization: user.token
            },
            body: JSON.stringify(settings)
        })
        quit()
    }

    return (
        <div className={`dark-div ${animation}`}>
            <Utils.Out click={quit}>
                <div className={`invite-people-div ${animation} ${waiting}`}>
                    <div style={{ textAlign: 'center' }} className="part-1">
                        <h3 style={{ fontSize: '20px', marginTop: 30 }}>Create Text Channel</h3>
                        <Plus onClick={quit} size={25} />
                    </div>
                    <div className="check-boxes">
                        <h5>CHANNEL TYPE</h5>
                        <div className="check">
                            <div className="checker"/>
                            <Hashtag size={28} />
                            <div className="channel-type">
                                <h4>Text Channel</h4>
                                <p>Post images, GIFs, opinions and puns</p>
                            </div>
                        </div>
                        <h5>CHANNEL NAME</h5>
                        <input onChange={handleInputChange} style={{ marginTop: 0, height: 35 }} className="change-server-name" />
                    </div>
                    <div className="create-server">
                        <button onClick={quit} style={{ color: "var(--font-primary)" }} className="create-server-button cancel-creating">Cancel</button>
                        <button onClick={create} className={`create-server-button create-channel ${!settings.name && "cant-click"}`}>Create Channel</button>
                    </div>
                </div>
            </Utils.Out>
        </div>
    )
}

export default CreateChannel
